import {
    Button,
    ModalPage,
    ModalPageHeader,
    ModalRoot,
    FormLayout,
    FormItem, Input, IconButton, Header, Title, PanelHeaderBack, Checkbox, Group, Text
} from "@vkontakte/vkui";
import {useState} from "react";
import {Icon24Add, Icon24Cancel} from "@vkontakte/icons";
import fs from "../Functions/Firebase";

const Modal = (p) => {
    const [type, setType] = useState(null);
    const [input, updateInput] = useState(0);
    const [marginable, setMarginable] = useState(false);
    const [loading, setLoading] = useState(false);
    const formatInput = (t) => {
        t = t.replace(/[^0-9]/g, '');
        if(t.charAt(0) === '0' && t > 0){
            t = t.substr(1);
        }
        if(t.length === 0){
            t = 0;
        }
        if(t === 0 || t > (isCurency ? availableC * 3 : availableS * 3)){
            setMarginable(false);
        }
        return parseInt(t);
    }
    const closeModal = () => {
        p.s(null, p.t);
        setTimeout(() => {
            setType(null);
            updateInput(0);
            setMarginable(false);
            setLoading(false);
        }, 250);
    }
    if(!p.t){
        return '';
    }
    if(p.t.ticker === ''){
        return '';
    }
    if(p.t.ticker === 'RUBRUB'){
        return '';
    }
    const makeDeal = () => {
        let a = 1;
        let c;
        let y = p.c;
        let x = p.p;
        if(type === 'buy'){
            a = -1;
        }
        if(isCurency){
            y.rub.count += a * input * p.t.price - 0.005 * input * p.t.price;
            switch (p.t.ticker) {
                case 'RUBCHF':
                    y.chf.count += a * - 1 * input;
                    y.chf.avgPrice = (p.c.chf.avgPrice * p.c.chf.count + input * a * -1 * p.t.price) / (p.c.chf.count + input * a * -1);
                    break;
                case 'RUBEUR':
                    y.eur.count += a * - 1 * input;
                    y.eur.avgPrice = (p.c.eur.avgPrice * p.c.eur.count + input * a * -1 * p.t.price) / (p.c.eur.count + input * a * -1);
                    break;
                case 'RUBUSD':
                    y.usd.count += a * - 1 * input;
                    y.usd.avgPrice = (p.c.usd.avgPrice * p.c.usd.count + input * a * -1 * p.t.price) / (p.c.usd.count + input * a * -1);
                    break;
                case 'RUBGBP':
                    y.gbp.count += a * - 1 * input;
                    y.gbp.avgPrice = (p.c.gbp.avgPrice * p.c.gbp.count + input * a * -1 * p.t.price) / (p.c.gbp.count + input * a * -1);
                    break;
                default:
                    return
            }

        }
        else{
            y.usd.count += input * a * p.t.price - p.t.price * input * 0.005;
            y.usd.avgPrice = (p.c.usd.avgPrice * p.c.usd.count + input * a * p.t.price) / (p.c.usd.count + input * a);
            let i = 0;
            for (const v of p.p) {
                if(v.ticker === p.t.ticker){
                    c = {
                        ticker: v.ticker,
                        avgPrice: (v.avgPrice * v.count + input * a * -1 * p.t.price) / (v.count + input * a * -1),
                        count: v.count + input * a * -1
                    }
                    x[i] = c;
                    break;
                }
                i++;
            }
            if(!c){
                c = {
                    ticker: p.t.ticker,
                    avgPrice: p.t.price,
                    count: input * a * -1
                }
                x.push(c);
            }
            for(let i = 0; i < x.length; i++) {
                if(x[i].count === 0){
                    x.splice(i, 1);
                }
            }
        }
        if(!x){
            x = [];
        }
        setLoading(true);
        fs.collection('users').doc('1').update({portfolio: x, currencies: p.c}).then((e) => {
            p.d(type, input, p.t.price, p.t.ticker);
            closeModal();
        });
    }

    let isCurency = ['RUBCHF', 'RUBUSD', 'RUBEUR', 'RUBRUB'].indexOf(p.t.ticker) >= 0;
    let availableS = parseInt(p.c.usd.count / p.t.price);
    let availableC = parseInt(p.c.rub.count / p.t.price);

    if(availableC < 0){
        availableC = 0;
    }
    if(availableS < 0){
        availableS = 0;
    }

    const isMarginable = () => {
        if(type === 'sell'){
            return p.t.count - input < 0;
        }
        else{
            if(isCurency){
                return availableC < input && availableC * 3 > input;
            }
            else{
                return availableS < input && availableS * 3 > input;
            }
        }
    }

    return <ModalRoot activeModal={p.v} onClose={closeModal}>
        <ModalPage
            id={"stock"}
            header={<ModalPageHeader left={<PanelHeaderBack onClick={closeModal}/>}><Header>{p.t.name}</Header></ModalPageHeader>}
            dynamicContentHeight
        >
            <Group header={<Header mode={'secondary'}> </Header>}>
                <FormItem>
                    NVIDIA ,      (3D)     .   ,         .
                </FormItem>
            </Group>
            <FormLayout>
                {!p.m && !isCurency ? <Button disabled stretched size={'l'} mode={'secondary'}> </Button> :
                    type === null ?
                    <FormItem style={{display: 'flex'}}>
                        <Button onClick={() => {setType('buy')}} mode={"commerce"} stretched size={"l"}></Button>
                        <div style={{width: 10}}/>
                        <Button onClick={() => {setType('sell')}} mode={"destructive"} stretched size={"l"}></Button>
                    </FormItem>
                    :
                    <div>
                        <FormItem>
                            <Title level={1} weight={'bold'}>{type === 'buy' ? '' : ''}</Title>
                        </FormItem>
                        <FormItem>
                            <Input disabled value={parseFloat(p.t.price).toFixed(2)}/>
                        </FormItem>
                        <FormItem>
                            <Title level={1} weight={'bold'}></Title>
                        </FormItem>
                        <FormItem bottom={` 
                            ${type === 'buy' ? (isCurency ? 
                                availableC : 
                                availableS)
                            : p.t.count > 0 ? parseInt(p.t.count) : parseInt(p.t.count) * -1
                        }
                        . (${type === 'buy' ? (isCurency ?
                            availableC * 3 :
                            availableS * 3)
                            : (isCurency ? availableC * 3 + parseInt(p.t.count) : availableS * 3 + parseInt(p.t.count))
                        }  )`}>
                            <Input after={
                                <div style={{display: 'flex'}}>
                                    {input !== 0 ? <IconButton icon={<Icon24Cancel onClick={() => updateInput(0)}/>}/> : ''}
                                    <IconButton icon={<Icon24Add onClick={() => {updateInput(input + 1)}}/>}/>
                                </div>
                            }
                                   onChange={(e) => updateInput(formatInput(e.target.value))}
                                   value={input}
                            />
                        </FormItem>
                        {isMarginable() ? <FormItem>
                            <Checkbox onChange={() => setMarginable(!marginable)}> </Checkbox>
                        </FormItem> : ''}
                        <FormItem>
                            <Button disabled={
                                loading ||
                                input === 0 ||
                                (isCurency && (
                                    (type === 'sell' && (
                                        (input > p.t.count && !marginable) ||
                                            input > p.t.count + availableC * 3
                                    )) ||
                                    (type === 'buy' && (
                                        (input > availableC && !marginable) ||
                                            input > availableC * 3
                                    ))
                                    //input > availableC && !marginable
                                )) ||
                                (!isCurency && (
                                    (type === 'sell' && (
                                        (input > p.t.count && !marginable) ||
                                        input > p.t.count + availableS * 3
                                    )) ||
                                    (type === 'buy' && (
                                        (input > availableS && !marginable) ||
                                        input > availableS * 3
                                    ))
                                ))
                                // (input > (isCurency ? availableC : availableS) && !marginable && !(type === 'sell' && input <= p.t.count)) ||
                                // (input > (isCurency ? availableC * 3 : availableS * 3) && !(type === 'sell' && input <= p.t.count))
                            } onClick={makeDeal} size={"l"} stretched loading={loading}>{type === 'buy' ? '' : ''}</Button>
                        </FormItem>
                    </div>
                }
            </FormLayout>
        </ModalPage>
    </ModalRoot>
}

export default Modal;